<script>
import store from "@/store";
import axiosInstance from "@/utils/axiosInstance";

export default {
  name: 'StudentsPage',

  data(){
    return{
      user_data: null
    }
  },

  computed: {
    userData(){
      return store.getters.getUserData;
    }
  },

  methods: {
    getUserData(){
      axiosInstance.get('st/data')
          .then(response =>{
            this.user_data = response.data
          })
          .catch(error =>{
            console.log("An error occurred while fetching user data: "+error)
          })
    }
  }
}
</script>

<template>
<div style="width: 100%">
  <div class="sidebar">
    <div class="flex-a" style="flex-direction: row">
      <div>
        <img src="public/st-augustine-college-logo.png" alt="profile image">
      </div>
      <h1>Name: {{userData.user_data.st_name}}</h1>
      <h1>Program: {{userData.user_data.st_program}}</h1>
    </div>
    <div>
      <h3>username: {{userData.username}}</h3>
    </div>
    <div><button @click="getUserData"></button></div>
    <div v-if="user_data">
      <div v-for="data in user_data" :key="data">
        {{data}}
      </div>
    </div>
  </div>

</div>
</template>

<style scoped>

</style>