import { createStore } from 'vuex'
import axios from "axios";
import jwt_decode from "jwt-decode";



const store = createStore({
    state: {
        authTokens: null,
        loginFormMessage: null,
        loginFormMessageColor: false,
        userData: null,
        isAuthenticated: false,
        rememberMe: null,
    },

    mutations: {
        setAuthTokens (state, token){
            state.authTokens = token;
        },
        setLoginFormMessage (state, message){
            state.loginFormMessage = message;
        },
        setLoginFormMessageColor (state, value){
            state.loginFormMessageColor = value;
        },
        setUserData(state, data){
            state.userData = data;
        },
        setRememberMe(state, data){
            state.rememberMe = data;
        },
        changeAuthenticationState(state, value){
            state.isAuthenticated = value;
        },
        hidLoginForm(state){
            setTimeout(()=>{
                const overlay = document.getElementById("overlay");
                const loginPopup = document.getElementById("login-popup");
                state.loginFormMessage = '';
                loginPopup.classList.remove('active')
                overlay.style.display = "none"

            }, 2000)

        },
        showLoginForm(state){
            if (sessionStorage.getItem('authTokens')){
                sessionStorage.removeItem('authTokens')
            }
            if (localStorage.getItem('authTokens')){
                localStorage.removeItem('authTokens')
            }
            state.isAuthenticated = false
            const overlay = document.getElementById("overlay");
            const loginPopup = document.getElementById("login-popup");
            if (overlay && loginPopup){
                loginPopup.classList.add('active')
                overlay.style.display = "block"
            }

        },
        logoutUser(state){
            if (sessionStorage.getItem('authTokens')){
                sessionStorage.removeItem('authTokens')
            }
            if (localStorage.getItem('authTokens')){
                localStorage.removeItem('authTokens')
            }
            // if (state.userData || state.authTokens){
            //     state.userData = null;
            //     state.authTokens = null
            // }
            state.loginFormMessageColor = false
            state.isAuthenticated = false
        }
        
    },

    actions: {
        async LoginUser(context, {username, password}){
            const formData = {username, password};
            try {
                const response = await axios.post("http://localhost:8000/api/token/", formData)
                const responseData = response.data;
                const userInfo = jwt_decode(responseData.access)
                if (response.status === 200){
                    if (userInfo.role){
                        context.commit('setAuthTokens', responseData)
                        context.commit('setUserData', userInfo)
                        context.commit('changeAuthenticationState', true)

                        // Tokens storage location
                        context.getters.getRememberMeState ? localStorage.setItem('authTokens',
                            JSON.stringify(responseData)): sessionStorage.setItem('authTokens', JSON.stringify(responseData))

                        context.commit('setLoginFormMessage', 'Login success')
                        context.commit('setLoginFormMessageColor', true)
                        context.commit('hidUserForm');

                    }else {
                        context.commit('setLoginFormMessage', 'You have not been assigned a role! Login again later')
                    }

                }

            }catch (e) {
                context.commit('setLoginFormMessage', 'Authentication error');
                console.log("Authentication error: "+e)
            }

        },

        async UpdateToken(context){
            try {
                const refreshToken = store.getters.getAuthTokens.refresh
                const response = await axios.post("http://localhost:8000/api/token/refresh/", {'refresh': refreshToken})
                const responseData = response.data;

                if (response.status === 200){
                    context.commit('setAuthTokens', responseData);

                    // Tokens storage Location
                    if (context.getters.getRememberMeState){
                        localStorage.setItem('authTokens', JSON.stringify(responseData))
                    }else {sessionStorage.setItem('authTokens', JSON.stringify(responseData))}
                    console.log('Token update successful')

                }else {
                    context.commit('logoutUser')
                    context.commit('setLoginFormMessage', 'Your session has expired! Login again')
                    context.commit('showLoginForm')
                }

            }catch (e) {
                context.commit('logoutUser')
                context.commit('setLoginFormMessage', 'Your session has expired! Login again')
                context.commit('showLoginForm')
                console.log("Authentication error: "+e)
            }

        },
    },

    getters: {
        getUserData(state){
            return state.userData;
        },
        getAuthenticationState(state){
            return state.isAuthenticated;
        },
        getAuthTokens(state){
            return state.authTokens;
        },
        getLoginFormMessage(state){
            return state.loginFormMessage;
        },
        getLoginFormMessageColor(state){
            return state.loginFormMessageColor;
        },
        getRememberMeState(state){
            return state.rememberMe;
        },

    }
})

export default store;