import { createRouter, createWebHistory } from 'vue-router';
import HomePage from "@/pages/home.vue";
import TeachersPage from "@/pages/teachers.vue";
import store from "@/store";
import StudentsPage from "@/pages/students.vue"

const router = createRouter({
    history: createWebHistory(process.env.BASE_URL),
    routes: [
        {
            path: '',
            name: 'root',
            component: HomePage
        },
        {
            path: '/home',
            name: 'home',
            component: HomePage
        },
        {
            path: '/tch',
            name: 'teacher',
            component: TeachersPage,
            props: true,
            meta: { requiresAuth: true },
        },
        {
            path: '/st',
            name: 'student',
            component: StudentsPage,
            props: true,
            meta: { requiresAuth: true },
        },

    ]
})

router.beforeEach((to, from, next) => {
  if (to.meta.requiresAuth && !store.getters.getAuthenticationState) {
    // Redirect to the login page if the route requires authentication
      console.log("Authentication required")
      store.commit('logoutUser')
      store.commit('setLoginFormMessage', "You must login before")
      store.commit('showLoginForm')
      if (store.getters.getAuthenticationState){
          next()
      }

  } else {
    next(); // Continue to the requested route
  }
});

export default router