<script>
import {onMounted } from "vue";
import store from "@/store";


export default {
  name: 'HeaderComp',

  setup(){
    onMounted( ()=>{
      const middlePosition = (window.outerHeight/2)+600;
      const header = document.getElementById("header");

      window.addEventListener("scroll", ()=>{
        const scrollY = window.scrollY;
        if (scrollY > middlePosition){
          header.style.position = "static"
        }else {
          header.style.position = "fixed"
        }
      })
    })
  },
  methods: {
    showForm(){
      return store.commit('showLoginForm')
    }
  },

  computed: {
    userData(){
      return store.getters.getUserData
    },
    isAuthenticated(){
      console.log("Authentication: "+store.getters.getAuthenticationState)
      return store.getters.getAuthenticationState;
    },

  }
}
</script>

