<script>
import { useHead } from "@vueuse/head";
import axios from 'axios'


export default {
  name: 'HomeComp',
  data(){
    return{
      latestdata: null,
      newsdata: null,
      announcementdata: null,
      youtubedata: null,
      twitterdata: null,
    }
  },
  setup(){
    document.title = "St.Augustine's - Home"
    useHead({
      meta: [
        {name: "description", content: "Welcome to St.Augustine's College where faith, knowledge and leadership intertwine to create a brighter future"}
      ]
    })

  },
  beforeMount() {
    axios.get("http://localhost:8000/latest")
        .then(response =>{
          this.latestdata = response.data
        })
        .catch(error =>{
          console.log("Error fetching data: "+ error)
        })

    axios.get("http://localhost:8000/news")
        .then(response =>{
          this.newsdata = response.data
        })
        .catch(error =>{
          console.log("Error fetching data: "+ error)
        })

    axios.get("http://localhost:8000/announcement")
        .then(response =>{
          this.announcementdata = response.data
        })
        .catch(error =>{
          console.log("Error fetching data: "+ error)
        })

    axios.get("http://localhost:8000/youtube")
        .then(response =>{
          this.youtubedata = response.data
        })
        .catch(error =>{
          console.log("Error fetching data: "+ error)
        })

    axios.get("http://localhost:8000/twitter")
        .then(response =>{
          this.twitterdata = response.data
        })
        .catch(error =>{
          console.log("Error fetching data: "+ error)
        })

  },

}
</script>

<template>

  <div id="carouselExampleIndicators" style="width: 100%; max-height: 570px; overflow: hidden" class="carousel slide flex-a" data-ride="carousel">
      <ol style="z-index: 1" class="carousel-indicators">
        <li v-for="item in latestdata" :key="item.id" data-target="#carouselExampleIndicators" :data-slide-to="item.slide_no" :class="{'active': item.slide_no===0}"></li>
      </ol>
      <div class="carousel-inner" style="width: 100%; height: auto">
        <div class="carousel-item c-item" v-for="item in latestdata" :key="item.id" :class="{'active': item.slide_no===0}">
          <div style="z-index: 1" class="carousel-caption d-md-block">
            <div v-if="item.btn===true" :key="item.id" class="latest-c flex-a">
              <h1 class="latest-item" style="color: red; z-index: 1; font-family: verdana, sans-serif; font-weight: bold; font-size: 1.5vw; text-transform: uppercase">{{ item.head }}</h1>
              <h3 class="latest-item" style="color: white; z-index: 1; margin-top: -5%; font-family: 'Times New Roman',sans-serif; font-size: 1.5vw">{{ item.title }}</h3>
              <button style="width: fit-content; z-index: 1; margin-bottom: 2%" class="btn-small-green">{{ item.btn_text }}</button>
            </div>
          </div>
          <img class="d-block w-100 c-img" :src="item.img_path" :alt="item.img_alt" aria-label="latest updates">
        </div>
      </div>
      <a class="carousel-control-prev c-btn" href="#carouselExampleIndicators" role="button" data-slide="prev">
        <span class="carousel-control-prev-icon" aria-hidden="true"></span>
        <span class="sr-only">Previous</span>
      </a>
      <a class="carousel-control-next c-btn" href="#carouselExampleIndicators" role="button" data-slide="next">
        <span class="carousel-control-next-icon" aria-hidden="true"></span>
        <span class="sr-only">Next</span>
      </a>
  </div>
  <div class="updates-c flex-a">
    <hr class="h-line">
    <div class="updates-item fas fa-newspaper"> <span class="updates-item">  NEWS & EVENTS</span></div>
    <hr class="h-line">
  </div>
  <div style="display: flex; width: 100%">
    <section class="news-section">
    <router-link to="news-item" v-for="item in newsdata" :key="item.id" class="news-item">
      <div class="news-image-container">
        <img class="news-image" :src="item.img_path" alt="News Image">
      </div>
      <div class="news-content">
        <h3 class="news-title">{{ item.title }}</h3>
        <div class="news-published fas fa-calendar-alt">: {{ item.date }}</div>
      </div>
    </router-link>
    </section>
    <section class="announcement">
      <div class="announcement-title-container">
        <h3 class="fas fa-bullhorn">  <span class="a-head">ANNOUNCEMENTS</span></h3>
      </div>
      <div style="display: flex; justify-content: left; flex-direction: column">
        <router-link v-for="item in announcementdata" :key="item.id" class="announcement-content" to="announcement">
          <p class="announcement-text">{{item.text}}</p>
          <p class="date fas fa-calendar-alt">: {{item.date}}</p>
      </router-link>
      </div>
    </section>
  </div>
  <div class="updates-c flex-a">
    <hr class="h-line">
    <div class="updates-item fas fa-play-circle"> <span class="updates-item">  MEDIA</span></div>
    <hr class="h-line">
  </div>
  <div class="media-container">
    <div class="youtube">
      <h3 style="color: red; font-size: 1vw" class="fab fa-youtube"><span style="color: red"> Youtube</span></h3>
      <div class="video-container">
        <div v-for="item in youtubedata" :key="item.id" class="video-wrapper">
          <div class="video" v-html="item.src"></div>
          <p class="video-title">{{ item.title }}</p>
        </div>
      </div>
    </div>
    <div class="twitter">
      <h3 style="color: red; font-size: 1vw" class="fab fa-twitter"><span style="color: red"> Twitter</span></h3>
      <div class="twitter-img-container">
        <img v-for="item in twitterdata" :key="item.id" :src="item.src" class="twitter-img" alt="twitter-image">
      </div>
    </div>
  </div>

</template>

<style scoped>

/* Carousel Section */

.latest-item{
  text-align: left;
  width: 100%;
  margin: 0;
  padding-left: 3%;
  padding-top: 3%;
  padding-bottom: 3%;
}

.c-item{
  width: 100%;
  height: auto;
}
.c-img{
  height: auto;
}
.c-btn{
  width: fit-content;
  height: fit-content;
  margin-top: 20%;
  color: white;
}
.carousel-indicators .active {
  background-color: lightseagreen;
}
.carousel-indicators li {
  background-color: red;
  opacity: 0.8;
}
.carousel-control-prev-icon {
  background-color: red;
}
.carousel-control-next-icon {
  background-color: red;
}

/* News section */

.fa-newspaper {
  margin-right: 10px;
