}
.announcement{
  width: 27%;
  display: flex;
  flex-direction: column;
  margin: 0.5%;
  border: lightseagreen 2px solid;
  align-items: center;
  justify-content: left;
  text-decoration: none;
  background-color: lightseagreen;
}
.date{
  margin-top: -3%;
  font-size: 0.8vw;
  color: red;
}
