  color: red;
}
.media-container{
  display: flex;
  width: 100%;
  margin-top: 5%;
}
.youtube{
  display: flex;
  width: 49%;
  flex-wrap: wrap;
  flex-direction: column;
  margin: 1%;

}
.twitter{
  display: flex;
  width: 49%;
  flex-direction: column;
  margin: 1%;
}
.twitter-img-container{
  display: flex;
  flex-direction: column;
  width: 100%;
  margin: 1%;
