}
.video-wrapper{
  display: flex;
  margin-bottom: 10%;
  width: 100%;
  flex-direction: column;
}
.video-container{
  width: 100%;
  display: flex;
  max-height: 600px;
  overflow: scroll;
  flex-direction: column;
}
.video-title{
  font-size: 1.2vw;
  height: 10%;
  font-family: verdana, sans-serif;
}
.video{
  width: 100%;
  height: 90%;
}
</style>

