import { createApp } from 'vue';
import App from './App.vue'
import router from "@/router/router";
import { createHead } from "@vueuse/head";
import 'bootstrap/dist/css/bootstrap.css';
import bootstrap from 'bootstrap/dist/js/bootstrap';
import '@fortawesome/fontawesome-free/css/all.css';
import store from "@/store";
import jwt_decode from "jwt-decode";

const sessionAuthTokens = JSON.parse(sessionStorage.getItem('authTokens'));
const localAuthTokens = JSON.parse(localStorage.getItem('authTokens'));
let refreshToken = null;
if (sessionAuthTokens) {
  store.commit('setAuthTokens', sessionAuthTokens);
  store.commit('setUserData', jwt_decode(sessionAuthTokens.access));
  store.commit('changeAuthenticationState', true);
  store.commit('setRememberMe', false)
  refreshToken = sessionAuthTokens.refresh;
} else if (localAuthTokens) {
  store.commit('setAuthTokens', localAuthTokens);
  store.commit('setUserData', jwt_decode(localAuthTokens.access));
  store.commit('changeAuthenticationState', true);
  store.commit('setRememberMe', true)
  refreshToken = localAuthTokens.refresh;
}

if (refreshToken){
  store.dispatch('UpdateToken')
}


const app = createApp(App);
const head = createHead();
app.use(router)
app.use(head)
app.use(bootstrap)
app.use(store)
app.mount('#app')
