import axios from "axios";
import store from "@/store";

const axiosInstance = axios.create({
    baseURL: 'http://localhost:8000/',
})


// request interceptor
axiosInstance.interceptors.request.use(
    async (config)=>{
        const accessToken = store.getters.getAuthTokens.access
        if (accessToken){
            config.headers.Authorization = `Bearer ${accessToken}`;
        }

        return config
    },
    (error)=>{
        return Promise.reject(error)
    }
)

// response interceptor
axiosInstance.interceptors.response.use(
    (response)=>{
        return response
    },
    async (error)=>{
        if (error.response.status === 401){
            try {
                const updateRefreshToken = await store.dispatch('UpdateToken');
                if (updateRefreshToken){
                    const newAccessToken = store.getters.getAuthTokens.access
                    const originalRequest = error.config
                    originalRequest.headers.Authorization = `Bearer ${newAccessToken}`;
                    return axiosInstance(originalRequest)
                }
            }catch (refreshError){
                store.commit('logoutUser')
                store.commit('setLoginFormMessage', 'Your session has expired! Login again')
                store.commit('showLoginForm')
            }
        }
        return Promise.reject(error)
    }
)

export default axiosInstance;
