<script>
import {onMounted} from "vue";
import store from "@/store";


export default {
  name: 'LoginForm',

  data() {
    return{
      username: null,
      password: null,
      rememberMe: null,
    }
  },

  setup(){
    onMounted( ()=>{
      const loginPopup = document.getElementById("login-popup");
      const close = document.getElementById("close-btn");
      const overlay = document.getElementById("overlay");

      if (close){
        close.addEventListener('click', ()=>{
        loginPopup.classList.remove("active")
        overlay.style.display = "none"
      })
      }
    })
  },

  methods: {
    authenticate(e){
      e.preventDefault();
      store.commit('setRememberMe', this.rememberMe)
      console.log('Radio value: '+this.rememberMe)
      store.dispatch('LoginUser', {username: this.username, password: this.password});
    },

  },

  computed: {
    loginFormMessage(){
      return store.getters.getLoginFormMessage;
    },
    loginFormMessageColor(){
      return store.getters.getLoginFormMessageColor;
    },

  }
}
</script>

<template>
  <div id="login-popup" class="login-popup">
    <span id="close-btn">X</span>
    <h2>Login</h2>
    <form @submit="authenticate">
      <h3 v-if="loginFormMessage && loginFormMessageColor" style="color: green">{{loginFormMessage}}</h3>
      <h3 v-else style="color: red">{{loginFormMessage}}</h3>
      <div class="form-group">
        <label for="username">Username</label>
        <input v-model="username" type="text" class="form-control" id="username" required>
      </div>
      <div class="form-group">
        <label for="password">Password</label>
        <input v-model="password" type="password" class="form-control" id="password" required>
      </div>
      <div>Remember Me <input type="radio" v-model="rememberMe"></div>
      <button type="submit" class="btn btn-primary">Submit</button>
    </form>
  </div>
  <div class="modal-overlay" id="overlay"></div>
</template>

<style scoped>
.modal-overlay {
  display: none; /* Initially hidden */
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.7); /* Semi-transparent background */
  z-index: 10; /* overlay appears above other elements */
}

.login-popup {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 40%;
  padding: 2%;
  background-color: #fff;
  border: 1px solid #ccc;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  z-index: 20;
  opacity: 0;
  transition: opacity 0.5s ease-in-out;
}
.login-popup.active{
  opacity: 1;
}

.login-popup h2 {
  text-align: center;
}

.login-popup form {
  margin-top: 20px;
}

.login-popup .form-group {
  margin-bottom: 10px;
}

.login-popup .form-group label {
  display: block;
  font-weight: bold;
}

.login-popup .form-group input {
  width: 100%;
  padding: 5px;
  border: 1px solid #ccc;
}

.login-popup .form-group button {
  width: 100%;
  padding: 10px;
  background-color: #007bff;
  color: #fff;
  border: none;
}
</style>